.game-board-body {
    font-family:sans-serif;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    text-align: center;
    min-height: 100vh;
    margin: 0;
}
.game {
    position: relative;
    padding-bottom: 20px;
}
.game-board {
    margin: 30px auto;
    position: relative;
    flex-wrap: wrap;
    overflow: hidden;
    width: 600px;
    height: 550px;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(4, 1fr);
    border-radius: 5px;
    grid-gap: 12px;
    gap: 12px;
    padding: 1rem;
}
.game-board:after {
    content: "";
    display: block;
}
.game-tile {
    cursor: pointer;
    background-color: #293752;
    box-shadow: rgba(50, 50, 93, 0.25) 0 50px 100px -20px, rgba(0, 0, 0, 0.3) 0 30px 60px -30px, rgba(10, 37, 64, 0.35) 0 -2px 6px 0 inset;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    border-radius: 5px;
    border: 2px;
    transition: 0.4s background-color, 0.4s box-shadow;
    overflow: hidden;
    padding: 0;
    transition-duration: 1s;
}
.game-tile:hover {
    background-color: #0f1529;
    box-shadow: 0 1px 4px rgba(0,0,0,0.2);
    transform: scale(1.2);
    -webkit-transform: scale(1.2);
    -moz-transform: scale(1.2);
    z-index: 0;
}
.game-score {
    position: absolute;
    left: 50%;
    top: 0;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding: 20px;
    background: #9ADE61;
    color: #fff;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    font-size: 2rem;
    font-weight: bold;
    line-height: 1rem;
    text-shadow: 0 1px 2px rgba(0,0,0,0.4);
}
.game button.button,
.game input[type=button].button,
.game input[type=submit].button {
    display: block;
    max-width: 20em;
    text-align: center;
    padding: 1.3em 3em;
    font-weight: bold;
    font-size: 1.1rem;
    color: #FFF;
    text-decoration: none;
    margin: 2em auto;
    background: tomato;
    border-radius: 3em;
    line-height: 1.1em;
    text-shadow: none;
    transition: 0.5s background-color, 0.5s box-shadow;
    box-shadow: 0 2px 5px rgba(255, 99, 71, 0.4);
    border:0;
    cursor: pointer;
}
.button-bg {
    flex-wrap: wrap;
    max-width: 300px;
    width: 100%;
