.gallery2 {
    -webkit-column-count: 3;
    -moz-column-count: 3;
    column-count: 3;
    -webkit-column-width: calc(33% - 33px);
    -moz-column-width: calc(33% - 33px);
    column-width:calc(33% - 33px);
    padding: 0 12px 12px;
    min-height: 100vh;
}
.header-gallery2 {
    word-break: break-word;
    color: #6cc464;
    font-family: 'Anonymous Pro', monospace;
    font-size: 25px;
    font-weight: 800;
    text-align: center;
    padding: 30px;
    letter-spacing: 2px;
    border-width: 2px;
    border-style: double;
    border-color: #6cc464;
    margin: 30px 0 20px;
}
.gallery2 .pics {
    transition: all 350ms ease;
    cursor: pointer;
    margin-bottom: 12px;
}
.gallery2 .pics:hover {
    filter: opacity(.8);
}

@media (max-width: 991px) {
    .gallery2 {
        -webkit-column-count: 2;
        -moz-column-count: 2;
        column-count: 2;

    }
}
@media (max-width: 480px) {
    .gallery2 {
        -webkit-column-count: 1;
        -moz-column-count: 1;
        column-count: 1;
        -webkit-column-width: calc(100% - 100px);
        -moz-column-width: calc(100% - 100px);
        column-width: calc(100% - 100px);
    }
}
/*model*/
.model {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.4);
    transition: opacity .4s ease,visibility .4s ease, transform .5s ease-in-out;
    visibility: hidden;
    opacity: 0;
    transform: scale(0);
    overflow: hidden;
    z-index: 999;
}
.model.open {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
}
.model img {
    width: auto;
    max-width: 100%;
    height: auto;
    max-height: 100%;
    display: block;
    line-height: 0;
    box-sizing: border-box;
    padding: 20px 0 20px;
    margin: 0 auto;
}
.model.open svg {
    position: fixed;
    top: 10px;
    right: 10px;
    width: 2rem;
    height: 2rem;
    padding: 5px;
    background-color: rgba(0,0,0,0.4);
    color: #FFFFFF;
    cursor: pointer;
}
.bounce {
    animation: bounce 2s infinite;
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-30px);
    }
    60% {
        transform: translateY(-15px);
    }
}